.TitleHeader {
  margin: 15px auto;
  text-align: center;
  color: #09d6c5;
}
.NotFound {
  color: #c80142;
}

.MovieGallery {
  display: grid;
  max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.MovieGalleryItem {
  overflow: hidden;
  position: relative;
  border-radius: 4px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.MovieGalleryItemImage {
  background-color: #e2be99;
  border: 1px solid #2d2727;
  display: block;
  vertical-align: top;
  max-width: 100%;
  min-width: 470px;
  height: 244px;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.MovieGalleryItemImage:hover {
  transform: scale(1.03);
  cursor: zoom-in;
}
.TitleFilm {
  position: absolute;
  color: #5db0c5;
  font-size: 25px;
  font-weight: 500;
  z-index: 10;
  bottom: 14px;
  left: 12px;
}
